import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { CalendarEvent } from '../../interfaces/calendar-event.interface';
import { MDBModalService, MDBModalRef } from 'angular-bootstrap-md';
import { EventModalComponent } from '../event-modal/event-modal.component';
import { CalendarView } from '../../utils/calendar-view';
import { take } from 'rxjs/operators';
import { format, getYear, getMonth, getDate } from 'date-fns';
import { MdbCalendarOptions } from '../../interfaces/calendar-options.interface';

@Component({
  selector: 'mdb-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.scss'],
})
export class MdbCalendarComponent implements OnInit {
  weekDaysDefault = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
  weekDaysShortDefault = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
  monthsDefault = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
    'September',
    'October',
    'November',
    'December',
  ];
  monthsShortDefault = [
    'Jan',
    'Feb',
    'Mar',
    'Apr',
    'May',
    'Jun',
    'Jul',
    'Aug',
    'Sep',
    'Oct',
    'Nov',
    'Dec',
  ];

  calendarOptions: MdbCalendarOptions = {
    firstDayOfWeek: 'Sunday',
    monthViewBtnTxt: 'Month',
    weekViewBtnTxt: 'Week',
    listViewBtnTxt: 'List',
    todayBtnTxt: 'Today',
    eventAddTitle: 'Add new event',
    eventEditTitle: 'Edit event',
    eventCancelBtnTxt: 'Cancel',
    eventAddBtnTxt: 'Add',
    eventEditBtnTxt: 'Edit',
    eventDeleteBtnTxt: 'Delete',
  };

  dayLabels: string[] = [];
  dayLabelsShort: string[] = [];
  weekDayIndex: number;

  @Input() startDate: Date = new Date();
  @Input() events: CalendarEvent[] = [];
  @Input() options: MdbCalendarOptions;
  @Input() editable = true;
  @Input() weekDays: string[] = this.weekDaysDefault;
  @Input() weekDaysShort: string[] = this.weekDaysShortDefault;
  @Input() months: string[] = this.monthsDefault;
  @Input() monthsShort: string[] = this.monthsShortDefault;
  @Input() defaultView: CalendarView;
  @Output() eventDeleted = new EventEmitter<CalendarEvent>();
  @Output() eventEdited = new EventEmitter<CalendarEvent>();
  @Output() eventAdded = new EventEmitter<CalendarEvent>();

  @Output() monthChanged = new EventEmitter<any>();
  @Output() weekChanged = new EventEmitter<any>();
  @Output() listChanged = new EventEmitter<any>();
  @Output() viewChanged = new EventEmitter<string>();

  modalRef: MDBModalRef;
  MS_IN_DAY = 24 * 60 * 60 * 1000;

  view = CalendarView;

  currentView: CalendarView = this.view.month;

  private _isInitialized = false;

  constructor(private modalService: MDBModalService) {}

  ngOnInit() {
    this.calendarOptions = this.options
      ? Object.assign(this.calendarOptions, this.options)
      : this.calendarOptions;
    this._changeDaysOrder();
    if (this.defaultView) {
      this.onViewChange(this.defaultView);
    }

    this._isInitialized = true;
  }

  private _changeDaysOrder() {
    const dayIndex = (this.weekDayIndex = this.weekDaysDefault.indexOf(
      this.calendarOptions.firstDayOfWeek
    ));

    if (dayIndex !== -1) {
      let index = dayIndex;
      for (let i = 0; i < this.weekDays.length; i++) {
        this.dayLabels.push(this.weekDays[index]);
        this.dayLabelsShort.push(this.weekDaysShort[index]);
        index = this.weekDaysDefault[index] === 'Saturday' ? 0 : index + 1;
      }
    }
  }

  getFormattedEvent(event: CalendarEvent) {
    return {
      id: event.id,
      name: event.name,
      startDate: format(event.startDate, 'YYYY-MM-DD, HH:mm:ss'),
      endDate: format(event.endDate, 'YYYY-MM-DD, HH:mm:ss'),
      color: event.color,
    };
  }

  formatDate(date: Date) {
    const year = getYear(date);
    const month = getMonth(date);
    const day = getDate(date);
  }

  onDayClick(day: any) {
    if (this.editable) {
      this.openAddModal(day);
    }
  }

  onEventClick(event: CalendarEvent) {
    this.openEditModal(event);
  }

  onViewChange(view: string) {
    this.currentView = this.view[view];

    this.viewChanged.emit(view);
  }

  onMonthChange(event: any) {
    this.monthChanged.emit(event);
  }

  onWeekChange(event: any) {
    this.weekChanged.emit(event);
  }

  onListChange(event: any) {
    this.listChanged.emit(event);
  }

  openAddModal(event: CalendarEvent) {
    const data = {
      title: this.calendarOptions.eventAddTitle,
      actionBtn: this.calendarOptions.eventAddBtnTxt,
      cancelBtn: this.calendarOptions.eventCancelBtnTxt,
      mode: 'add',
      editable: this.editable,
      event: this.getFormattedEvent(event),
    };
    this.modalRef = this.modalService.show(EventModalComponent, { data });

    this.modalRef.content.eventData.pipe(take(1)).subscribe((newEvent: CalendarEvent) => {
      this.eventAdded.emit(newEvent);
    });
  }

  openEditModal(event: CalendarEvent) {
    const data = {
      title: this.calendarOptions.eventEditTitle,
      actionBtn: this.calendarOptions.eventEditBtnTxt,
      cancelBtn: this.calendarOptions.eventDeleteBtnTxt,
      mode: 'edit',
      editable: this.editable,
      event: event,
    };
    this.modalRef = this.modalService.show(EventModalComponent, { data });

    this.modalRef.content.eventData.pipe(take(1)).subscribe((editedEvent: CalendarEvent) => {
      this.eventEdited.emit(editedEvent);
    });

    this.modalRef.content.eventDeleted.pipe(take(1)).subscribe((deletedEvent: CalendarEvent) => {
      this.eventDeleted.emit(deletedEvent);
    });
  }
}
