import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  Renderer2,
  ViewChildren,
  QueryList,
  ElementRef,
  AfterViewInit,
} from '@angular/core';
import {
  startOfWeek,
  addDays,
  getDate,
  getDay,
  getMonth,
  getYear,
  startOfDay,
  addHours,
  addMinutes,
  subDays,
  endOfDay,
  isToday,
  isWeekend,
  format,
} from 'date-fns';
import { CalendarEvent } from '../../interfaces/calendar-event.interface';
import { getWeekHourEvents, getWeekAllDayEvents } from '../../utils/event-utils';
import { MdbCalendarOptions } from '../../interfaces/calendar-options.interface';

@Component({
  selector: 'mdb-calendar-week-view',
  templateUrl: './calendar-week-view.component.html',
  styleUrls: ['./calendar-week-view.component.scss'],
})
export class MdbCalendarWeekViewComponent implements OnInit, AfterViewInit {
  @ViewChildren('dayEl') days: QueryList<any>;
  @ViewChildren('fullDayEl') fullDays: QueryList<any>;

  @Input()
  get initDate(): Date {
    return this._initDate;
  }
  set initDate(date: Date) {
    this._initDate = date;

    if (this._isInitialized) {
      const initDay = (this.initDay = startOfWeek(startOfDay(date), {
        weekStartsOn: this.weekDayIndex,
      }));
      this.weekView = this.createWeekView(initDay);
    }
  }
  private _initDate: Date;

  @Input()
  get events() {
    return this._events;
  }
  set events(events: CalendarEvent[]) {
    this._events = events;
    this.weekView = this.createWeekView(this.initDay);
  }
  private _events: CalendarEvent[];
  @Input() weekDaysShort: string[] = [];
  @Input() monthsShort: string[] = [];
  @Input() weekDayIndex = 0;
  @Input() options: MdbCalendarOptions;

  @Output() dateClicked = new EventEmitter<any>();
  @Output() eventClicked = new EventEmitter<any>();
  @Output() viewChanged = new EventEmitter<string>();
  @Output() weekChanged = new EventEmitter<any>();

  startDate: Date;
  endDate: Date;

  weekView: any;
  initDay: Date;

  dayCells: HTMLElement[] = [];
  fullDayCells: HTMLElement[] = [];
  columns: HTMLElement[] = [];

  fullDaySelectionStart: Date;
  fullDaySelectionEnd: Date;

  fullDayEditing: boolean;
  dayEditing: boolean;

  selectionStartDate: Date;
  selectionEndDate: Date;

  dragStart: any;
  isDragging: boolean;
  dragEnd: any;

  private _isInitialized = false;

  constructor(private renderer: Renderer2) {}

  ngOnInit() {
    const initDay = (this.initDay = startOfWeek(startOfDay(this.initDate), {
      weekStartsOn: this.weekDayIndex,
    }));
    this.weekView = this.createWeekView(initDay);

    this._isInitialized = true;
  }

  ngAfterViewInit() {
    this.fullDayCells = this.fullDays.toArray().map((el: ElementRef) => el.nativeElement);

    this.dayCells = this.days.toArray().map((el: ElementRef) => el.nativeElement);

    for (let i = 1; i <= 7; i++) {
      this.dayCells.forEach((row: any) => {
        this.columns.push(row.children[i]);
      });
    }
  }

  trackByFn(index) {
    return index;
  }

  previous() {
    this.initDay = subDays(this.initDay, 7);
    this.weekView = this.createWeekView(this.initDay);
    this.weekChanged.emit({
      startDate: this.startDate,
      endDate: this.endDate,
    });
  }

  next() {
    this.initDay = addDays(this.initDay, 7);
    this.weekView = this.createWeekView(this.initDay);
    this.weekChanged.emit({
      startDate: this.initDay,
      endDate: this.endDate,
    });
  }

  goToToday() {
    this.initDay = startOfWeek(startOfDay(new Date()), { weekStartsOn: this.weekDayIndex });
    this.weekView = this.createWeekView(this.initDay);
    this.weekChanged.emit({
      startDate: this.startDate,
      endDate: this.endDate,
    });
  }

  fullDayMouseDown(event: any, day: any) {
    if (this.dayEditing) {
      return;
    }
    this.fullDayEditing = true;
    this.dragStart = this.fullDayCells.indexOf(event.target);
    this.isDragging = true;
    this.fullDaySelectionStart = day.startDate;
  }

  fullDayMouseUp(event: any, day: any) {
    this.dragEnd = this.fullDayCells.indexOf(event.target);
    this.fullDaySelectionEnd = day.endDate;
    this.fullDayEditing = false;

    const calendarEvent = {
      name: 'New event',
      startDate: this.fullDaySelectionStart,
      endDate: this.fullDaySelectionEnd,
      color: 'info',
    };
    if (this.dragStart !== this.dragEnd) {
      this.dateClicked.emit(calendarEvent);
    }
    this.isDragging = false;

    if (this.dragEnd !== 0) {
      this.fullDaySelectRange();
    }

    this.fullDayClearSelection();
  }

  fullDaySelectRange() {
    this.fullDayClearSelection();

    if (this.dragEnd > this.dragStart) {
      if (this.dragEnd + 1 < this.dragStart) {
        this.fullDayCells
          .slice(this.dragEnd, this.dragStart + 1)
          .forEach((cell) =>
            this.renderer.setStyle(cell, 'background-color', 'rgba(69,82,110,.3)')
          );
      } else {
        this.fullDayCells
          .slice(this.dragStart, this.dragEnd + 1)
          .forEach((cell) =>
            this.renderer.setStyle(cell, 'background-color', 'rgba(69,82,110,.3)')
          );
      }
    }
  }

  fullDayClearSelection() {
    this.fullDayCells.forEach((cell) => this.renderer.removeStyle(cell, 'background-color'));
  }

  fullDayMouseMove(event: any) {
    event.preventDefault();
    if (this.dayEditing) {
      return;
    }
    if (this.isDragging) {
      this.dragEnd = this.fullDayCells.indexOf(event.target);
      this.fullDaySelectRange();
    }
  }

  onMouseDown(event: any, day: any) {
    if (this.fullDayEditing) {
      return;
    }

    this.dayEditing = true;
    this.dragStart = this.columns.indexOf(event.target);
    this.isDragging = true;
    this.selectionStartDate = day.startDate;
  }

  onMouseUp(event: any, day: any) {
    this.dragEnd = this.columns.indexOf(event.target);
    this.selectionEndDate = day.endDate;
    this.dayEditing = false;

    const calendarEvent = {
      name: 'New event',
      startDate: new Date(this.selectionStartDate),
      endDate: new Date(this.selectionEndDate),
      color: 'info',
    };
    if (this.dragStart !== this.dragEnd) {
      this.dateClicked.emit(calendarEvent);
    }
    this.isDragging = false;

    if (this.dragEnd !== 0) {
      this.selectRange();
    }

    this.clearSelection();
  }

  selectRange() {
    this.clearSelection();

    if (this.dragEnd > this.dragStart) {
      if (this.dragEnd + 1 < this.dragStart) {
        this.columns
          .slice(this.dragEnd, this.dragStart + 1)
          .forEach((cell) =>
            this.renderer.setStyle(cell, 'background-color', 'rgba(69,82,110,.3)')
          );
      } else {
        this.columns
          .slice(this.dragStart, this.dragEnd + 1)
          .forEach((cell) =>
            this.renderer.setStyle(cell, 'background-color', 'rgba(69,82,110,.3)')
          );
      }
    }
  }

  clearSelection() {
    this.columns.forEach((cell) => this.renderer.removeStyle(cell, 'background-color'));
  }

  onMouseMove(event: any) {
    event.preventDefault();

    if (this.fullDayEditing) {
      return;
    }
    if (this.isDragging) {
      this.dragEnd = this.columns.indexOf(event.target);
      this.selectRange();
    }
  }

  onEventClick(event: CalendarEvent) {
    const eventCopy = {
      id: event.id,
      name: event.name,
      startDate: format(event.startDate, 'YYYY-MM-DD, HH:mm:ss'),
      endDate: format(event.endDate, 'YYYY-MM-DD, HH:mm:ss'),
      color: event.color,
    };
    this.eventClicked.emit(eventCopy);
  }

  onDateClick(date: any) {
    const newEvent = {
      name: 'New event',
      startDate: date.startDate,
      endDate: date.endDate,
      color: 'info',
    };
    this.dateClicked.emit(newEvent);
  }

  onViewChange(view: string) {
    this.viewChanged.emit(view);
  }

  createWeekView(initDate: Date) {
    const firstDay = initDate;
    const lastDay = addDays(firstDay, 6);

    this.startDate = firstDay;
    this.endDate = lastDay;

    const period = {
      start: `${getDate(firstDay)} ${this.monthsShort[getMonth(firstDay)]}, ${getYear(firstDay)}`,
      end: `${getDate(lastDay)} ${this.monthsShort[getMonth(lastDay)]}, ${getYear(lastDay)}`,
    };

    const allDayRow = [];
    const weekRows = [];
    let row = [];
    let date: Date;
    let month: number;
    let year: number;
    let day: string;
    let dayNumber: number;
    let startDate: Date;
    let endDate: Date;
    let dayStart: Date;
    let dayEnd: Date;

    for (let i = 0; i < 24; i++) {
      row = [];

      for (let j = 0; j < 7; j++) {
        date = addDays(firstDay, j);
        month = getMonth(date) + 1;
        year = getYear(date);
        day = this.weekDaysShort[getDay(date)];
        dayNumber = getDate(date);
        startDate = addHours(startOfDay(date), i);
        endDate = addMinutes(startDate, 59.99);
        dayStart = startOfDay(date);
        dayEnd = endOfDay(date);

        row.push({
          startDate: startDate,
          endDate: endDate,
          isToday: isToday(date),
          isWeekend: isWeekend(date),
          events: getWeekHourEvents(this.events, startDate, endDate, dayStart, dayEnd),
        });

        if (i === 1) {
          allDayRow.push({
            startDate: dayStart,
            endDate: dayEnd,
            isToday: isToday(date),
            isWeekend: isWeekend(date),
            day: day,
            dayNumber: dayNumber,
            month: month,
            events: getWeekAllDayEvents(this.events, dayStart, dayEnd, dayStart, dayEnd),
          });
        }
      }
      weekRows.push({ row });
    }
    return { allDayRow, weekRows, period };
  }
}
